class DashboardComponent {
    constructor(dashboardService, paymentService, pharmacyService, router) {
        this.dashboardService = dashboardService;
        this.paymentService = paymentService;
        this.pharmacyService = pharmacyService;
        this.router = router;
        this.isLoader = false;
        this.totalCount = 0;
        this.completed = 0;
        this.unCompleted = 0;
        this.page = 0;
        this.limit = 10;
        this.result = null;
        this.billsList = [];
    }
    ngOnInit() {
        this.getData();
        this.getAllBills();
        this.getAllPrescriptions();
    }
    getAllPrescriptions() {
        this.pharmacyService.getPrescriptions(1, 10, this.selectedDate).subscribe((res) => {
            this.result = res;
            console.log(this.result);
        });
    }
    getData() {
        this.isLoader = true;
        this.dashboardService.getPharmacyDashBoard().subscribe((res) => {
            if (res.status == '200') {
                this.completed = res.data.completed;
                this.unCompleted = res.data.unComplete;
                this.totalCount = this.completed + this.unCompleted;
                this.isLoader = false;
            }
        });
    }
    gotoHospitalProfile(id) {
        this.router.navigate([`/app/ticket`]);
    }
    getAllBills() {
        this.paymentService.getAll(this.page, this.limit).subscribe((res) => {
            if (res.status == '200') {
                console.log(res);
                this.billsList = res.data.doc;
            }
            else {
                this.billsList = [];
            }
        });
    }
    onClickPrescription(id) {
        this.router.navigate(['app/prescription/', id]);
    }
    onClick(id) {
        this.router.navigate(['/app/payment/invoice/', id]);
    }
    getSubTotal(i) {
        let subTotal = 0;
        this.billsList[i].items.forEach(a => subTotal += (a.price * a.quantity));
        return subTotal;
    }
    applyDiscount(i) {
        return this.getSubTotal(i) - (this.getSubTotal(i) * (this.billsList[i].discount / 100));
    }
    onDateSelection(date) {
        this.selectedDate = new Date(Date.UTC(date.year, date.month - 1, date.day)); // If you don't see the error play (add\remove) the `+1` on this line
        console.log(this.selectedDate, date);
        this.getAllPrescriptions();
    }
}


