class ProfileComponent {
    constructor(pharmacyService, router, activatedRoute, chatService, authService, notifyService) {
        this.pharmacyService = pharmacyService;
        this.router = router;
        this.activatedRoute = activatedRoute;
        this.chatService = chatService;
        this.authService = authService;
        this.notifyService = notifyService;
        this.isLoader = false;
        this.tab = 1;
    }
    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            console.log(params['id']);
            this.get(params['id']);
        });
    }
    get(id) {
        this.isLoader = true;
        this.pharmacyService.getById(id).subscribe((res) => {
            console.log(res.data);
            if (res.status == '200') {
                this.pharmacy = res.data;
                this.isLoader = false;
            }
            else {
                this.router.navigate(['app/pharmacy']);
            }
        });
    }
    onclickTab(tab) {
        this.tab = tab;
    }
    chat() {
        this.chatService.addfriend(this.pharmacy.user._id).subscribe((res) => {
            if (+res.status === 200) {
                this.router.navigate([`app/inbox/${this.pharmacy.user._id}`]);
            }
        });
    }
    markAsActive() {
        this.authService.toggleactive(this.pharmacy.user._id).subscribe((res) => {
            if (res.status == '200') {
                this.pharmacy.user.active = !this.pharmacy.user.active;
                this.notifyService.success(res.data.msg);
            }
        });
    }
    goToEdit() {
        this.router.navigate(['/app/pharmacy/edit/', this.pharmacy._id], { queryParams: { returnUrl: this.router.url } });
    }
}


